<template>
  <div class="cards page-back mx-auto" :style="{height:computedHeight}">
    <h2 class="bold-x-lg my-5" style="font-family: KB_C3">ÎßàÏùºÎ¶¨ÏßÄ Î©òÌä∏</h2>
    <div style="padding:0 5%;">
      <div class="p-4 mt-5">
        <h3 class="lg p-3" style="text-align: left; font-family: KB_C2">ÎßàÏùºÎ¶¨ÏßÄ Ìï≠Î™©</h3>
        <div class="p-2">
          <div class="input-gray p-3" style="text-align:left;">
            <span class="lg2" style="width: 100%; text-align: left;">{{mile_name}}&nbsp;ÎßàÏùºÎ¶¨ÏßÄ</span>
          </div>
        </div>
      </div>
      <div class="p-4">
        <div class="d-flex justify-content-between align-items-center">
          <h3 class="lg p-3" style="text-align: left; font-family: KB_C2">Ï∂îÏ≤ú Î©òÌä∏</h3>
          <button @click="addComment">
            <span class="lg2 mr-5 brown" style="text-align:right;"><i class="bi bi-plus-lg"></i>&nbsp;Ï∂îÍ∞ÄÌïòÍ∏∞</span>
          </button>
        </div>
        <div v-for="(recommand, index) in mileRecommands" :key="index" class="p-2 d-flex justify-content-between align-items-center">
          <div class="input-gray p-3" style="width: 95%; text-align:left;">
            <input v-model="recommand.mile_mention" class="lg2" placeholder="Ï∂îÏ≤ú Î©òÌä∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî" style="text-align: left; width: 100%;"/>
          </div>
          <button @click="deleteComment(index)">
            <span class="lg brown"><i class="bi bi-dash-lg"></i></span>
          </button>
        </div>
      </div>
    </div>
    <div style="padding-top: 10%">
      <button @click="addCommentAction" class="btn-green" style="width:8vw; height: 3vw; font-size:1.2vw; font-family: KB_C2;">Îì±Î°ù</button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
export default {
  name: 'CommentMieageeAdminView',
  data(){
    return {
      mile_no: '',
      mile_name: '',
      baseHeight: 80,
      increment: 10,
      mileRecommands: []
    }
  },
  computed :{
    ...mapGetters('mile', ['getMileInfo']),
    ...mapGetters('login', ['getLoginInfo']),
    ...mapGetters('mileRecommand', ['getMileRecommandInfo']),
    loginInfo(){
      return this.getLoginInfo;
    },
    mileInfo() {
      return this.getMileInfo;
    },
    computedHeight(){
      return `${this.baseHeight + this.mileRecommands.length * this.increment}vh`;
    },
    mileRecommandInfo(){
      return this.getMileRecommandInfo;
    }
  },
  methods: {
    ...mapActions('mile', ['fetchMileInfo']),
    ...mapActions('mileRecommand', ['getMileRecommand','updateRecommands', 'addRecommands', 'deleteRecommands']),
    addComment(){
      const newRecommand = {
        mile_mention: '', 
        mile_no: this.mileRecommands.length > 0 ? this.mileRecommands[0].mile_no : null, 
        mile_link: this.mileRecommands.length > 0 ? this.mileRecommands[0].mile_link : null, 
        mile_recommand_no: null
      };
      this.mileRecommands.push(newRecommand);
    },
    async deleteComment(index){
      const recommand = this.mileRecommands[index];
      if(recommand.mile_recommand_no){
        // DBÏóê ÏûàÎäî Î©òÌä∏Î•º ÏÇ≠Ï†úÌï† Í≤ΩÏö∞ 
        const response = await this.deleteRecommands(recommand);
        if(response && response.data.success){
          this.mileRecommands.splice(index, 1); // Ìï¥Îãπ Ïù∏Îç±Ïä§Ïùò Ìï≠Î™©ÏùÑ Î∞∞Ïó¥ÏóêÏÑú ÏÇ≠Ï†ú 
          console.log('ÏÇ≠Ï†ú ÏôÑÎ£å');
        }else{
          console.log('ÏÇ≠Ï†ú Ïã§Ìå®');
        }
      }else{
        // DBÏóê ÏóÜÎäî Î©òÌä∏Î•º ÏÇ≠Ï†úÌï† Í≤ΩÏö∞
        this.mileRecommands.splice(index, 1); // Ìï¥Îãπ Ïù∏Îç±Ïä§Ïùò Ìï≠Î™©ÏùÑ Î∞∞Ïó¥ÏóêÏÑú ÏÇ≠Ï†ú 
      }
    },
    async addCommentAction(){
      if(this.mileRecommands!=null){
        for(const recommand of this.mileRecommands){
          if(recommand.mile_recommand_no){
            // recommandÍ∞Ä Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎ©¥ ÏóÖÎç∞Ïù¥Ìä∏ 
            await this.updateRecommands(recommand);
          }else{
            // recommandÍ∞Ä Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏúºÎ©¥ ÏÉàÎ°ú Ï∂îÍ∞Ä 
            await this.addRecommands(recommand);
          }
        }
        this.showAlert('Ï∂îÏ≤ú Î©òÌä∏Í∞Ä Îì±Î°ùÎêòÏóàÏäµÎãàÎã§', 'success', '#');
      }else{
        this.showAlert('Ï∂îÏ≤ú Î©òÌä∏ Îì±Î°ùÏù¥ Ïã§Ìå®ÌñàÏäµÎãàÎã§', 'error', '#')
      }
    },
    showAlert(t, i, r) {
      this.$swal({
        title: t,
        icon: i,
        scrollbarPadding: false
      }).then((result) => {
        if(result.isConfirmed){
          if(r == '#'){
            location.reload(); // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ®
          }else{
            this.$router.push(r);
          }
        }
      })
    },
  },
  async created(){
    const user_no = this.loginInfo ? this.loginInfo.user_no : null;
    if(user_no){
      await this.fetchMileInfo(user_no);
      const mileInfo = this.getMileInfo;
      if(mileInfo){
        this.mile_name = mileInfo.mile_no;
      }else{
        console.error('ÎßàÏùºÎ¶¨ÏßÄ Ïù¥Î¶ÑÏùÑ Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.');
      }
    }else{
      console.error('user_noÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
    }
    const mile_no = this.loginInfo ? this.loginInfo.mile_no : null;
    this.mile_no = mile_no;
    if(mile_no){
      await this.getMileRecommand(mile_no);
      const mileRecommandInfo = this.getMileRecommandInfo;
      if(mileRecommandInfo){
        this.mileRecommands = mileRecommandInfo;
      }else {
        console.error('Ï∂îÏ≤úÎ©òÌä∏Î•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.');
      }
    }
  },
};
</script>

<style scoped>
.page-back {
  width: 70%;
  margin-top: 5%;
}
</style>
